.contacts__map {
  display: grid;
  grid-template-columns: repeat(2,1fr);
}

.contacts__contacts {
  padding-top: 27px;  
  padding-right: 210px;
  font-size: 18px;
  line-height: 28px;
}

.contacts__phone {
  font-size: 18px;
  line-height: 28px;
}

.contacts__item {
  margin-bottom: 27px;
  padding-left: 61px;

  &--call {
    background-image: url("../img/icon-call.png");
    background-repeat: no-repeat;
    background-position: left;
    height: 40px;
  }

  &--location {
    background-image: url("../img/icon-location.png");
    background-repeat: no-repeat;
    background-position: left;
    height: 40px;
  }
}

.contacts__map-picture {
  grid-row: 1/3;
  grid-column: 2/3;
}