.presentation {
  background-color: #dceaf8;  
  padding-bottom: 100px;
}

.presentation__wrapper {
  display: grid;
  grid-template-columns: 1fr 420px;
}

.presentation__text {
  padding-top: 15px;
}

.presentation__title {
  font-size: 48px;
  line-height: 74px;
  font-weight: 700;
  margin: 0;
  width: 650px;
  padding-bottom: 56px;
}

.presentation__description {
  font-size: 30px;
  font-weight: 700;
}

.presentation__call {
  padding-top: 35px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.presentation__image {
  position: relative;
  background-color: #93badf;
  border-radius: 50%;

  & img {
    position: absolute;
    bottom: -10px;
    left: -10px;
  }
}
